@import url("https://fonts.googleapis.com/css2?family=Hachi+Maru+Pop&family=Poppins:ital,wght@0,400;1,200&display=swap");

.Quote {
  font-family: "Poppins", sans-serif;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);
}

.Quote__text {
  width: 50%;
  text-align: center;
  font-family: "Hachi Maru Pop", cursive;
}

.Quote__author {
  font-style: italic;
  font-weight: 200;
}

.Quote__buttons {
  margin-top: 40px;
}

.Quote__buttons__prev,
.Quote__buttons__new {
  background: #caafab;
  border: 2px solid #857688;
  border-radius: 30px;
  padding: 10px 0;
  width: 130px;
  cursor: pointer;
  font-size: 16px;
}

.Quote__buttons__prev {
  margin-right: 20px;
}
